import React, { PureComponent } from 'react';

import logo from '../logo.png';

export default class Footer extends PureComponent {
  render() {
    return (
      <header className="logo-area">
        <img src={logo} alt="Practical JavaScript" />
        <h3> </h3>
        <p>   </p>
      </header>
    )
  }
}



