import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { BarLoader } from 'react-spinners';
import { Helmet } from 'react-helmet';

import { createMarkup, toTop } from '../helper';

export default class SingleContent extends Component {
  state = {
    data: '',
    shouldUpdate: true
  }

  componentWillMount() {
    const { props: { match: { params: { id } } } } = this;
    axios(`https://with.zonayed.me/wp-json/wp/v2/posts/${id}`).then(({data}) => this.setState({data, shouldUpdate: false}));
    toTop();
  }

  shouldComponentUpdate() {
    const { state: { shouldUpdate }} = this;
    return shouldUpdate;
  }

  setLastRead = (id, topic, title) => {
    localStorage.setItem('lastRead', `${id}-${topic}-${title.rendered.replace('-', ' ')}`);
  }

  render() {
    const { state: { data } } = this;
    if(!data) return <div className="loading-bar"><BarLoader /></div>

    const { props: { match: { params: { topic, id } } } } = this;
    const { state: { data: { title, content, link } } } = this;
    this.setLastRead(id, topic, title);
    return (
      <React.Fragment>
        <Helmet>
          <title>{title.rendered} -  </title>
        </Helmet>
        <div className="btns">
          <Link to="/" className="btn btn-home"></Link>
          <a rel="noopener noreferrer" target="_blank" href={`https://github.com/zonayedpca/js.zonayed.me/issues/new?title=${title.rendered}&labels=%E0%A6%B2%E0%A7%87%E0%A6%96%E0%A6%BE%E0%A7%9F%20%E0%A6%AD%E0%A7%81%E0%A6%B2&body=  `} className="btn btn-error"> ?</a>
        </div>
        <div className="single-content">
          <h1 className="title" dangerouslySetInnerHTML={createMarkup(title.rendered)} />
          <div className="post">
            <span dangerouslySetInnerHTML={createMarkup(content.rendered)}/>
            <p>    <a rel="noopener noreferrer" target="_blank" href={`${link}`}> </a>    <a rel="noopener noreferrer" target="_blank" href="https://with.zonayed.me/bn"> </a>       <a rel="noopener noreferrer" target="_blank" href="https://with.zonayed.me/bn/%E0%A6%B8%E0%A6%BE%E0%A6%AC%E0%A6%B8%E0%A7%8D%E0%A6%95%E0%A7%8D%E0%A6%B0%E0%A6%BE%E0%A6%87%E0%A6%AC-%E0%A6%95%E0%A6%B0%E0%A7%81%E0%A6%A8/"></a>                  !</p>
          </div>
        </div>
      </React.Fragment>
    )
  }
}



